*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 21px;
  color: #666666;
  font-weight: 400; }

.link {
  color: #666666;
  text-decoration: none; }

.button {
  display: block;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none; }
  .button--blue {
    font-size: 16px;
    line-height: 18px;
    color: #ffffff;
    background-color: #00b2ff;
    border: none; }
    .button--blue:hover, .button--blue:focus {
      color: #00b2ff;
      background-color: #ffffff;
      outline: none; }
    .button--blue:active {
      color: rgba(255, 255, 255, 0.3); }

.same-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.main-header__wrapper {
  position: relative;
  z-index: 3;
  display: flex;
  width: 74.79vw;
  margin: 0 auto;
  max-width: 1436px;
  padding-top: 1.88vw;
  padding-left: 1.3vw;
  justify-content: space-between; }

.main-header__communication-methods {
  display: flex;
  margin-bottom: 0.47vw; }

.main-header__contacts {
  display: flex;
  width: 25vw;
  max-width: 480px; }

.main-header__element {
  align-self: center;
  padding-right: 3.23vw; }

.main-header__link {
  position: relative;
  padding-right: 1vw;
  padding-left: 2.1vw; }
  .main-header__link--mail::before, .main-header__link--phone::before {
    position: absolute;
    background-repeat: no-repeat;
    content: ""; }
  .main-header__link--mail::before {
    top: 3px;
    left: 1px;
    width: 19px;
    height: 14px;
    background-image: url("../img/mail.svg"); }
  .main-header__link--phone::before {
    top: 1px;
    left: 6px;
    width: 18px;
    height: 18px;
    background-image: url("../img/phone-call.svg"); }

.main-header__button {
  width: 9.38vw;
  margin-top: 0.15vw;
  max-width: 180px; }
